html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

address {
  font-style: normal;
}

body {
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.71;
}

.page-header {
  background-color: #00f;
  margin-bottom: 30px;
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header-page-container {
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.search-form {
  gap: 15px;
  display: flex;
}

.search-form input {
  width: 100%;
  border-radius: 4px;
  padding: 10px 20px;
}

.search-form button {
  background-color: #fff;
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 20px;
  font-size: 14px;
  display: block;
}

.search-form button:hover, .search-form button:focus {
  color: var(--button-white-color);
  background-color: #ff0;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  background-color: #00f;
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-bottom: 100px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 20px;
  font-size: 14px;
  display: block;
}

.load-more:hover, .load-more:focus {
  background-color: #ff0;
}

